import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { HomeService } from 'src/app/core/services/home.service';
import { Content } from 'src/app/models/content';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-slider-cms',
  templateUrl: './slider-cms.component.html',
  styleUrls: ['./slider-cms.component.css']
})
export class SliderCmsComponent implements OnInit {
  get f() { return this.sliderForm.controls; }

  sliderForm!: FormGroup;
  submitted:boolean=false;

  file!: File;
  height: string = "0";
  width: string = "0";
  imageSrc!: string;
  toggleActualDimension: string = "hidden";
  constructor(private homeService: HomeService, private toastr: ToastrService) {
  }

  ngOnInit() {
    this.sliderForm = new FormGroup({
      'sliderTitle': new FormControl('', [Validators.required, Validators.minLength(2), Validators.maxLength(50)])
      , 'sliderSubtitle': new FormControl('', [Validators.required])
      , 'sliderDescription': new FormControl('', [Validators.required])
      , 'sliderImage': new FormControl('', [Validators.required])
    });
  }

  calculateImageDimension() {
  }

  onFileChange(event: any) {
    const reader = new FileReader();

    if (event.target.files && event.target.files.length) {
      this.file = event.target.files[0];
      reader.readAsDataURL(this.file);
      reader.onload = rs => {
        this.imageSrc = reader.result as string;

        const img = new Image();
        img.src = URL.createObjectURL(this.file);
        img.onload = (e: any) => {
          this.height = e.path[0].height;
          this.width = e.path[0].width;
          this.toggleActualDimension = "visible";
        };

      }
    }

  }
  onSubmitSliderForm() {
    this.submitted = true;
    if (this.sliderForm.invalid) {
      return;
    }
    
    let  content: Content = this.sliderForm.value;
    this.homeService.saveSliderText(content)
    .subscribe((res)=>{
      if (res.status === 'success') {
        //this.message = res.message;
        // this.toasterService.pop('success', appToaster.successHead, res.message);
        this.toastr.success('slider text updated successfully');
      }
      else
      this.toastr.error('Oops! something error occured');
    });
    console.log("this.sliderForm:", JSON.stringify(this.sliderForm.value));

  }

  uploadSliderImage(){
    this.homeService.uploadImage(this.file).subscribe(()=>{
      console.log("uploaded File: ", this.file);
    });
  }

}
